.two-halves-block {
	display: flex;
	flex-direction: column;
	margin-bottom: 2rem;
	border: 1px solid #ddd;
	border-radius: 8px;
	overflow: hidden;
}

.image-side {
	flex: 1;
	min-width: 300px;
	background-color: #f0f0f0;
}

.image-side img {
	max-width: 100%;
	height: auto;
	display: block;
}

.content-side {
	flex: 1;
	min-width: 300px;
	padding: 20px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-color: #fff;
}

.eyebrow {
	font-size: 14px;
	color: #888;
	text-transform: uppercase;
	margin-bottom: 10px;
}

.title {
	font-size: 24px;
	font-weight: bold;
	margin-bottom: 10px;
}

.description {
	font-size: 16px;
	margin-bottom: 20px;
}

.button {
	display: inline-block;
	padding: 10px 20px;
	font-size: 16px;
	color: #fff;
	background-color: #0073aa;
	text-decoration: none;
	border-radius: 4px;
	transition: background-color 0.3s;
}

.button:hover {
	background-color: #005a8d;
}
